<template>
  <div class="container-fluid p-0 m-0">
    <div class="ms-0 pt-3 ps-3 d-flex d-md-block flex-nowrap picture-mobile">
      <!-- Pictures -->
      <div
        v-for="(set, index) in pictures"
        :key="index"
        class="d-flex  flex-md-wrap flex-nowrap align-items-start justify-content-start justify-content-md-between"
      >
        <div class="col-md-6 d-none d-md-block col-12 home-btn">
          <span v-if="index === 0">PICTURES</span>
        </div>
        <div
          :id="`photoset-${index}`"
          @click="changeSet(index)"
          class="col-md-4 pe-3 col-auto works-font"
        >
          {{ set.primary.title }}
        </div>
        <div class="col-1 d-none d-md-block p-0"></div>
        <div
          v-if="index === openSet"
          class="col-12 mt-md-2 mt-1 justify-content-center"
        >
          <div class="col-auto d-flex flex-wrap justify-content-center">
            <table :style="'width:' + set.items.length * 15 + '%' " class="d-none d-md-block">
              <tr>
                <th
                  @click="openOverlay(image)"
                  v-for="image in set.items"
                  :key="image"
                >
                  <img
                    class="img-fluid photo lazyload"
                    :data-src="image.image.url"
                    :height="image.image.dimensions.height"
                    :width="image.image.dimensions.width"
                    alt=""
                  />
                </th>
              </tr>
            </table>
            <div class="mobile-picture-gallery flex-row d-flex d-md-none">
              <img
                @click="openOverlay(image)"
                v-for="image in set.items"
                :key="image"
                class="lazyload pe-2"
                :data-src="image.image.url"
                :height="image.image.dimensions.height"
                :width="image.image.dimensions.width"
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- Overlay modal -->
    <transition name="fade">
      <div
        v-if="overlayOpen"
        @click="overlayOpen = false"
        class="overlay-modal"
      >
        <div
          class="container-fluid h-100 d-flex justify-content-center align-items-center"
        >
          <img
            :data-src="overlayImage.image.url"
            class="img-fluid overlay-image lazyload"
            alt=""
          />
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pictures: [],
      dataLoaded: false,
      openSet: null,
      overlayImage: null,
      overlayOpen: false,
    };
  },
  methods: {
    getContent() {
      this.$prismic.client.getSingle("pictures").then((response) => {
        this.pictures = response.data.body;
        this.dataLoaded = true;
        this.openSet = response.data.body.length - 1;
      });
    },
    changeSet(el) {
      this.openSet = el;

      document.getElementById(`photoset-${el}`).scrollIntoView({
        behavior: "smooth",
        block: "center",
        inline: "center",
      });
    },
    openOverlay(el) {
      this.overlayImage = el;
      this.overlayOpen = true;
    },
  },
  created() {
    this.getContent();
  },
};
</script>

